.movieDetail {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
  padding: 1rem 1.5rem 2rem 3rem;
  background-color: #111111;
  z-index: 5;
}

.movieDetail img {
  display: block;
  height: 400px;
  width: 100%;
}

.text {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.text h2 {
  padding: 2rem 0;
  border-bottom: 2px solid #fff;
}

.number {
  font-weight: 700;
  font-size: 18px;
}
